:root {
  --font-body: Arial, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --whiteLight: hsl(0, 8%, 75%);
  --black: hsl(0, 0%, 0%);
  --violet: hsl(203, 84%, 17%);
  --gray:hsl(0, 0%, 53%);
  --grayLight:hsla(0, 0%, 91%, 1);
  --grayDark: hsla(0, 0%, 89%, 1);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.banner {
  position: absolute;
  top: 0;
}
.banner__image {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  width: 100%;
  gap: 0.1rem;
}
.banner__image img {
  width: 100%;
}
.banner__playbtn {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, 50%);
  cursor: pointer;
}
@media (max-width: 50em) {
  .banner__playbtn {
    width: 3rem;
  }
}
@media (max-width: 26.563em) {
  .banner__playbtn {
    width: 1.8rem;
  }
}
.banner__text {
  display: none;
}

.banner.show {
  z-index: 500;
  width: 100%;
}
