.banner.show .banner__image.show {
  gap: 0;
  overflow: hidden;
}
.banner.show .show img {
  opacity: 0;
}
.banner.show .banner__playbtn {
  display: none;
}
.banner.show .banner__text {
  display: block;
  position: absolute;
  top: 60%;
  transform: translate(-50%, -50%);
  color: #fff;
  left: 50%;
  font-family: "STIX Two Text";
  font-style: normal;
  text-align: center;
}
.banner.show .banner__text :nth-child(1) {
  font-weight: 400;
  font-size: 1.75rem;
  letter-spacing: 0.26em;
}
