.grid_1__cols {
  display: grid;
}

.grid_2__cols {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 50em) {
  .grid_2__cols {
    grid-template-columns: 1fr;
  }
}

.navbar {
  overflow: hidden;
  z-index: 1000;
  position: relative;
}
.navbar_menu {
  height: 3.125rem;
  width: 3.125rem;
  position: relative;
  transition: 0.4s ease;
}
.navbar_menu__togglebox {
  position: relative;
  display: block;
  margin-left: 3.25rem;
  height: 3.125rem;
  width: 3.125rem;
  border-radius: 3.125rem;
}
@media (max-width: 26.563em) {
  .navbar_menu__togglebox {
    margin-left: 1.25rem;
    width: 2.5rem;
  }
}
.navbar_menu__togglebox .logo {
  position: absolute;
  height: 0.25rem;
  width: 2.1875rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--whiteLight);
  border-radius: 3.125rem;
}
.navbar_menu__togglebox .logo::before, .navbar_menu__togglebox .logo::after {
  position: absolute;
  content: "";
  height: 0.25rem;
  width: 2.1875rem;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--whiteLight);
  border-radius: 3.125rem;
  transition: 0.3s;
}
.navbar_menu__togglebox .logo::before {
  top: 0.75rem;
}
.navbar_menu__togglebox .logo::after {
  bottom: 0.5rem;
}
.navbar_menu .active {
  background-color: var(--white);
  border-radius: 3.125rem;
  border: solid 0.1875rem var(--violet);
}
.navbar_menu .active .logo {
  width: 0;
}
.navbar_menu .active .logo::before {
  background-color: var(--violet);
  width: 2rem;
  transform-origin: right;
  transform: translate(-20px, 0px) rotate(45deg);
}
.navbar_menu .active .logo::after {
  background-color: var(--violet);
  width: 2rem;
  transform-origin: right;
  transform: translate(-20px, -2px) rotate(-45deg);
}
.navbar_menu__profil {
  text-align: center;
  margin: 0.9375rem 0 2.8125rem;
}
.navbar_menu__items {
  width: 100%;
}
.navbar_menu__items li a {
  color: var(--black);
}
.navbar_menu__items li a .item {
  border-bottom: solid 0.0625rem;
  padding-bottom: 0.6875rem;
}
.navbar_menu__items li a span {
  position: relative;
  display: block;
  letter-spacing: 2;
  padding-left: 1rem;
  margin-top: 1.3125rem;
  width: 100%;
}
.navbar .block {
  margin: 0;
  height: 100vh;
  width: 25%;
  background-color: var(--white);
}
@media (max-width: 50em) {
  .navbar .block {
    width: 50%;
  }
}
@media (max-width: 26.563em) {
  .navbar .block {
    width: 100%;
  }
}

.histoire {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  gap: 1.75rem;
}
.histoire_book__content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 65%;
  min-width: 15.625rem;
  max-width: 25rem;
  height: 30rem;
  position: relative;
  perspective: 625rem;
  transition: 1s;
}
@media (max-width: 50em) {
  .histoire_book__content {
    perspective: 62.5rem;
    max-width: 44rem;
    width: 100%;
    height: 40rem;
  }
}
.histoire_book__content .book {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: 1s;
  transform-style: preserve-3d;
  transform-origin: left;
}
@media (max-width: 50em) {
  .histoire_book__content .book {
    width: 65%;
    transform-origin: bottom;
  }
}
.histoire_book__content .book .face-front, .histoire_book__content .book .face-back {
  display: grid;
  width: 100%;
  height: 100%;
  padding: 1rem;
  box-sizing: border-box;
  overflow: hidden;
  cursor: pointer;
}
.histoire_book__content .book .face-front h1, .histoire_book__content .book .face-back h1 {
  text-align: center;
  align-self: center;
}
.histoire_book__content .book .face-front .page, .histoire_book__content .book .face-back .page {
  gap: 0.625rem;
  height: min-content;
}
.histoire_book__content .book .face-front p, .histoire_book__content .book .face-back p {
  margin: 0;
  font-size: 0.6875rem;
  height: min-content;
  text-align: justify;
}
@media (max-width: 50em) {
  .histoire_book__content .book .face-front p, .histoire_book__content .book .face-back p {
    font-size: 0.75rem;
  }
}
@media (max-width: 50em) {
  .histoire_book__content .book .face-front, .histoire_book__content .book .face-back {
    padding: 1rem 2.1875rem;
  }
}
.histoire_book__content .book .face-front {
  box-shadow: inset 0.1875rem 0 1.25rem -0.4375rem var(--black);
  border-top-right-radius: 0.1875rem;
  border-bottom-right-radius: 0.1875rem;
  background-color: var(--grayLight);
}
.histoire_book__content .book .face-front .btn-next {
  justify-self: end;
  align-self: end;
}
.histoire_book__content .book .face-back {
  background-color: var(--grayDark);
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: inset 0.1875rem 0 1.25rem -0.4375rem var(--black);
  transform: translateZ(-1px) rotateY(180deg);
  border-top-left-radius: 0.1875rem;
  border-bottom-left-radius: 0.1875rem;
}
.histoire_book__content .book .face-back .btn-back {
  align-self: end;
}
.histoire_book__content .book #portada {
  background-image: url("https://res.cloudinary.com/digital-x-sarl/image/upload/v1665565970/mariage/sans%20image/home.png");
}
.histoire_book__content .book #portada-back {
  background-image: url("https://res.cloudinary.com/digital-x-sarl/image/upload/v1665565969/mariage/sans%20image/end.png");
}
.histoire_book__content .book #portada, .histoire_book__content .book #portada-back {
  background-position: center;
  margin: 0;
  padding: 0;
}
@media (max-width: 50em) {
  .histoire_book__content .book #portada, .histoire_book__content .book #portada-back {
    background-size: cover;
  }
}
.histoire h3 {
  font-weight: 300;
  font-size: 0.875rem;
  line-height: 1.0625rem;
  letter-spacing: 0.37rem;
}

.reset {
  transform: rotateX(0rem);
}

.trnsf-reset {
  transform: translateX(12rem);
}

.trnsf {
  transform: translateX(12.125rem);
}
@media (max-width: 71.8125em) {
  .trnsf {
    transform: translateX(12rem);
  }
}
@media (max-width: 50em) {
  .trnsf {
    transform: translateX(0rem);
  }
}